@import url('./reset.scss');
@import url('../assets/fonts/montserrat/webfont-montserrat.css');

$break-laptop: 1390px;
$break-tablet: 1190px;
$break-smart:800px;
$break-phone:640px;

$font: 'Montserrat';
$textcolor: #ffffff;
$animation: .8s ease-in-out;

@mixin flex($hor, $vert, $flow) {
  display: flex;
  justify-content: $hor;
  align-items: $vert;
  flex-flow: $flow;
}

body {
  @include flex(center, center, column);
  position: relative;
  font-family: $font;
  color: $textcolor;
  background: url('../assets/img/bg3.jpg');
  background-repeat:no-repeat;
  background-size: cover;
  height:100vh;
  &::before{
    content: '';
    position: absolute;
    width: 100%;
    display: flex;
    background: linear-gradient(180deg, rgba(8, 15, 26, 0.59) 0%, rgba(17, 17, 46, 0.46) 100%);
    position: absolute;
    z-index: 95;
    top: 0px;
    left: 0px;
    height: 100%;
  }

  @media screen and (max-width: $break-tablet) {
    height:auto;
  }

  @media screen and (max-width: $break-phone) {
    height:100vh;
    &::before{
      height:100vh;
    }
  }
}

.wrapper {
  @include flex(center, center, column);
  box-sizing: border-box;
  max-width:1440px;
  width:100%;
  margin: 0 auto;
  padding: 40px 90px;
  z-index: 100;
    @media screen and (max-width: $break-smart) {
      padding: 30px 30px;
    }

    @media screen and (max-width: $break-phone) {
      height:100vh;
    }
}

header{
  @include flex(space-between, center, row);
  width: 100%;
  position:relative;
  z-index:300;

  .options {
    width:18%;
    min-width: 220px;

    ul {
      @include flex(space-between, center, row);
      box-sizing: border-box;
      color: $textcolor;
      font-style: normal;
      font-weight: bold;
      font-size: 14px;
      text-transform: uppercase;

      li{
        box-sizing: border-box;
        border-radius: 5px;
      }
    }

    .options__bgupdate {
      cursor:pointer;
      padding: 12px 15px;
      background: url('../assets/img/switch-bg.png');
      background-repeat: no-repeat;
      background-size: cover;

      &::before{
        content:'';
        @include flex(center, center, column);
        width: 15px;
        height: 20px;
        background: url('../assets/img/pictUpdate.svg');
        border-radius: 50%;
        transition: transform $animation;
      }
      &:hover::before{
        transform: rotate(180deg);
      }
    }

    .options__lang{
      background-color: #858c96;

      select {
        border: none;
        background: url('../assets/img/select-arrow-down.svg') no-repeat 73%;
        text-transform: uppercase;
        -webkit-appearance: none;
        cursor:pointer;
        font-weight: bold;
        font-size: 14px;
        color:$textcolor;
        padding: 14px 36px 14px 14px;
        text-align: left;

        option {
          background-color: #858c96;
          color:$textcolor;
        }
      }
    }

    .options__tempswitch{
      @include flex(center, center, row);

      button {
        position: relative;
        padding: 14px 17px;
        border: none;
        background: #858c96;
        color:#fff;
        font-style: normal;
        font-weight: bold;
        font-size: 14px;
        cursor:pointer;

        &::before{
          content:'\00B0';
          position: absolute;
          top: 15px;
          left: 10px;
          font-size: 15px;
        }


        &:first-child{
          border-top-left-radius: 5px;
          border-bottom-left-radius: 5px;
        }

        &:last-child{
          border-top-right-radius: 5px;
          border-bottom-right-radius: 5px;
        }

        &.active {
          background: #555c66;
        }
      }

    }
  }

  .search {
    width: 31%;
    form{
      @include flex(center, initial, row);
      input[type=text]{
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        display: flex;
        align-items: center;
        color: #FFFFFF;
        padding: 11px 15px;
        background: rgba(76, 82, 85, 0.4);
        border-top: 1px solid rgba(228, 227, 227, 0.2);
        border-left: 1px solid rgba(228, 227, 227, 0.2);
        border-bottom: 1px solid rgba(228, 227, 227, 0.2);
        border-right: 1px solid rgba(76, 82, 85, 0.4);

        box-sizing: border-box;
        border-top-left-radius: 5px;
        border-bottom-left-radius: 5px;
        width: 276px;
        transition: transform $animation;

        &::placeholder {
          color:#fff;
        }
        &:focus {
          background: rgba(76, 82, 85, 0.8);
          transition: transform $animation;
        }
        &:hover {
          background: rgba(76, 82, 85, 0.8);
          transition: transform $animation;
        }
      }

      button {
        border: none;
        background: none;
        padding: 11px 7px;
        margin: 0 0 0 -31px;
      }

      input[type=submit]{
        background: rgba(174, 181, 185, 0.5);
        color: #fff;
        font-style: normal;
        font-weight: bold;
        font-size: 14px;
        line-height: 17px;
        text-align: center;
        text-transform: uppercase;
        border: none;
        padding: 0 22px;
        cursor: pointer;
        border-top-right-radius: 5px;
        border-bottom-right-radius: 5px;
        transition: transform $animation;

        &:hover {
          background: rgba(174, 181, 185, 0.9);
          transition: transform $animation;
        }
      }
    }
  }

  @media screen and (max-width: $break-smart) {
    .options {
      width: 22%;
    }

    .search {
      width: 60%;
    }
  }

  @media screen and (max-width: $break-smart) {
    flex-wrap: wrap-reverse;
    justify-content: center;
    .search {
      margin: 13px 0 11px 0;
    }
  }
}

main {
  @include flex(space-around, center, row);
  width: 100%;
  margin: 57px 0;
  position:relative;
  z-index:300;

  .weather {
    @include flex(space-between, initial, column);
    width: 70%;

    h2 {
      font-style: normal;
      font-weight: bold;
      font-size: 44px;
      line-height: 54px;
      text-transform: uppercase;
      color: #FFFFFF;
    }

    h4 {
      font-style: normal;
      font-weight: 600;
      font-size: 24px;
      line-height: 29px;
      color: #FFFFFF;
    }

    .weather__info {
      @include flex(space-around, center, row);
      width: 64%;
      min-width: 570px;

      p {
        font-style: normal;
        font-weight: bold;
        font-size: 100px;
        line-height: 270px;
        text-transform: uppercase;
        color: #FFFFFF;

        &::after {
          content: '\00B0';
        }
      }

      img {
        width: 14%;
        margin: 73px 0 0 -121px;
      }

      ul {
        li{
          font-style: normal;
          font-weight: bold;
          font-size: 20px;
          line-height: 33px;
          text-transform: uppercase;

          .feels__like__value {
            &::after {
              content:'\00B0';
            }
          }

          .wind__value {
            &::after {
              content:' m/s';
              text-transform: lowercase;
            }
          }

          .humidity__value {
            &::after {
              content:' %';
              text-transform: lowercase;
            }
          }


        }
      }

    }

    .weather__week {
      @include flex(space-evenly, center, row);
      width: 70%;
      margin: 44px 0 0 0;
      min-width: 580px;

      li {
        font-style: normal;
        text-transform: uppercase;
        color: #FFFFFF;

        p {
          font-weight: bold;
          font-size: 22px;
          line-height: 27px;
        }

        div {
          span {
            font-weight: 600;
            font-size: 45px;
            line-height: 85px;
            &::after {
              content:'\00B0';
            }
          }
          img {
            width: 48px;
          }
        }
      }
    }
  }

  .location {
    @media screen and (max-width: $break-phone) {
      display:none;
    }
      div {
        iframe {
          border-radius:10px;
          margin:10px;
        }
      }
      ul{
        margin: 0 auto;
        display: table;
        li{
          span.deg{
            &::after {
              content:'\00B0';
            }
          }
          span.min{
            &::after {
              content:"'";
            }
          }
        }
      }
  }
  @media screen and (max-width: $break-tablet) {
    flex-flow: column;

    .weather {
      align-items: center;

      h2 {
        font-size: 30px;
        line-height: 40px;
      }

      h4 {
        font-size: 20px;
        line-height: 17px;
      }

      .weather__info {
        min-width: 475px;
        p {
          font-size: 100px;
          line-height: 158px;
        }

        img {
          width: 10%;
          margin: 50px 0 0 -108px;
        }

        ul {
          li {
            font-size: 19px;
            line-height: 26px;
          }
        }

      }

      .weather__week {
        margin: 20px 0 0 0;
        p{
          font-size: 18px;
          line-height: 0px;
        }
        li{
          div{
            margin: 0 auto;
            display: table;
            span{
              font-size: 36px;
              line-height: 65px;
            }
            img {
              width: 34px;
            }
          }
        }
      }
    }
  }
  @media screen and (max-width: $break-phone) {
    margin: 23px 0;

    .weather {
      h2 {
        font-size:19px;
      }
      h4 {
        font-size: 13px;
        line-height: 11px;
      }

      .weather__info {
        min-width: 320px;
        p {
          font-size: 63px;
          line-height: 183px;
        }

        img {
          margin: 29px 0 0 -47px;
        }

        ul {
          li {
            font-size: 15px;
            line-height: 20px;
          }
        }
      }

      .weather__week {
        margin: 35px 0 0 0;
        min-width: 320px;
        li {
          P{
            font-size: 11px;
            line-height: 5px;
          }
        }
      }
    }
  }
}
